import { Payment } from './../../models/payment';
import { EnvService } from 'src/app/services/env.service';
import { UserService } from 'src/app/services/user.service';
import { IonRefresher } from '@ionic/angular';
import { Component, OnInit } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-user-payments',
  templateUrl: './user-payments.page.html',
  styleUrls: ['./user-payments.page.scss'],
})
export class UserPaymentsPage implements OnInit {
  refresher:IonRefresher = null;
  getVerifiedText = Payment.getVerifiedText;
  constructor(public userService: UserService
    , public env:EnvService) {}
  ngOnInit(
    
  ) {
    this.userService.myPayments
      .getUpdateNotifier(this)
      .subscribe((result) => {
        this.refresher && this.refresher.complete();
        
      });
      this.userService.myPayments.request();
    }
  refresh(event) {
    this.userService.myPayments.request();
    this.refresher = event.target;
  }
  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
  }

}
