export class Payment {
    id:number;
    amount:number;
    is_verified:boolean;
    user_id:number;
    reference_id:string;
    created_at:Date;

    public static getVerifiedText(payment:Payment)
    {
        if(payment.is_verified)
        {
            return 'تأیید شده';
        }
        return 'تأیید نشده';
    }
}
